.project {
    width: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #fcfafa;
    .img {
        width: 150px;
        height: 150px;
        background-color: #77779a;
        display: flex;
        justify-content: center;
        align-items: center;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;
        &::before {
            position: absolute;
            content: '';
            background-color: black;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0.4;
        }
    }
}


