.projectsBlock {
  /*min-height: 100vh;*/
  background-color: #fff5ec;
  padding: 80px 0;

  .projectsContainer {
    flex-direction: column;
    justify-content: flex-start;

    .projects {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      flex-wrap: wrap;
      margin-top: 30px;
      @media screen and (max-width: 1129px) {
        justify-content: center;
      }

    }
  }
}




